<template>
    <div class="tools">
        <span @click="addFile" class="glyphicon glyphicon-plus" aria-hidden="true"></span>
        <span @click="favouriteFile" class="glyphicon glyphicon-star-empty" aria-hidden="true"></span>
        <span @click="removeFile" class="glyphicon glyphicon-remove" aria-hidden="true"></span>
    </div>
</template>
<script>
import { mapMutations, mapState, mapGetters } from 'vuex'

export default {
    methods: { 
        addFile:function(){
            this.$store.commit('add_file');
        },
        ...mapMutations ({
            removeFile: 'remove_File',
            favouriteFile: 'favourite_File',
        })
    }
}
</script>
<style scoped>
    * {
        margin: 0;
        padding: 0;

    }
    .tools {
        position: absolute;
        top: 0;
        left: 0;
        width: 150px;
        height: 100%;
        background: rgb(212, 211, 212);
    }
    div > span {
        display: block;
        width: 150px;
        height: 150px;
        font-size:80px;
        text-align: center;
        line-height: 150px;
    }
</style>





