<template>
    <div class="list" style="border-right:1px solid #f5f5f5;">
        <h1>备注| COLIGO</h1>
        <div class="btn-group" role="group" aria-label="...">
            <button 
                type="button" 
                class="btn btn1 btn-default" 
                @click="xianshiListxinjian($event)" 
                :class="{clickyinying : $store.state.zhuangtaixinjian}"
                >所有的笔记</button>
            <button 
                type="button" 
                class="btn btn-default"
                @click="xianshiListfavorite($event)" 
                :class="{clickyinying : $store.state.zhuangtaifavourite}"
                >喜欢的笔记</button>
        </div>
        <h2 
            v-show="$store.state.zhuangtaixinjian"
            style="line-height: 45px;font-size: 16px"
            class="dangqianbiji" 
            v-for="(item, index) in dataList" 
            :key="index" 
            :class="{cunzaiclass: nowList === item}"
            @click="changeNownote({item, index})"
            >
        {{item.title}}</h2>
        <h3 
            v-show="$store.state.zhuangtaifavourite"
            style="line-height: 45px;font-size: 16px"
            class="dangqianbiji" 
            v-for="(item, index) in farList" 
            :key="index" 
            :class="{cunzaiclass: nowList === item}"
            @click="changeNownote({item, index})"
            >
        {{item.title}}</h3 >


    </div>
</template>
<script>
import { mapMutations, mapState, mapGetters } from 'vuex'

export default {
    computed: {
    // 返回store中的值
        dataList () {
            return this.$store.state.listArr;
        },
        nowList(){
            return this.$store.state.nowList;
        },
        farList () {
            return this.$store.state.favouriteList;
        },
        
      
    },
    methods: {
        ...mapMutations({
            xianshiListxinjian: 'changeStatus',
            xianshiListfavorite: 'changeStatus',
            changeNownote: 'set_nowNote'
        }),
        // xianshiListxinjian: function(e){
        //     console.log (e);
        //     this.$store.commit('changeStatus', e.target.innerHTML);
        // }
    }

    // data () {
    //     return {
    //         title: '',
    //     }

    // },
    // computed: {
    //     title () {
    //         console.log ($store.state.title);
    //         return ($store.state.title == '') ? '新笔记' :  $store.state.title;
    //     }
    // }
}
</script>
<style>

    .list {
        top: 0;
        left: 150px;
        width: 300px;
        position: absolute;
        height: 100%;
        background: #f5f5f5;
    }

    h1 {
        text-transform: uppercase;
        text-align: center;
    }
    .btn-group > button {
        position: relative;
        left: 50px;
        margin-bottom: 10px;

    }
    /* .btn1 :focus {
        background-color: white;
    } */
    .dangqianbiji {
        margin: 0;
        padding: 0;
        width: 299px;
        height: 50px;
        background: white;
        border-bottom:1px solid #f5f5f5;
    }
    .cunzaiclass, .clickyinying {
        color: #fff;
        background-color: #10b181;
    }
</style>




