<template>
    <div class="note" v-show="true">
        <form action="">
            <div class="form-group">
                <label for="exampleInputEmail1">文章标题</label>
                <input :value="nowList.title" type="email" class="form-control" id="exampleInputEmail1" placeholder="title" @input="changeTitle">
            </div>
            <div class="form-group">
                <label for="exampleInputPassword1">正文</label>
                <textarea 
                    :value="nowList.text"
                    @input="changeNote" 
                    class="form-control" 
                    rows="3" 
                    placeholder="请输入内容">
                </textarea>
            </div>
        </form>

    </div>
</template>
<script>
import { mapMutations, mapState, mapGetters } from 'vuex'

export default {
    // data () {
    //     return {
    //         title: '',
    //     }
    // },
    computed: {
      // 返回store中的值
        nowList () {
            return this.$store.state.nowList;
        }
    },
    methods: {
        changeNote: function (e) {
            var text=e.target.value;
            this.$store.commit('set_Text',text);
        },
        changeTitle:function(e){
            var text=e.target.value;
            this.$store.commit('set_Title',text);
        }
    },
    // watch :{
    //     title () {
    //         this.$store.commit ('changeTitle', this.title);
    //     }
    // },
}
</script>
<style>
    .note {
        position: absolute;
        top: 0;
        left: 450px;
        color: #86aee8;
    }

</style>




