import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

var listArr= [{
    title: '',
    text: '',
    isFavourite: '',
}];


export default new Vuex.Store({
    state: {
        // value: 'Hello Vue',
        // num: 10,
        // title: '新笔记',
        nowTag : {},
        zhuangtaixinjian: false,
        zhuangtaifavourite: false,
        listArr: [{
            title: '新笔记',
            text: '',
            isFavourite: '',
        }],
        favouriteList: [],
        nowList: listArr[0],
        nowIndex: 0,
    },
    //mutations 是为了修改state中的数据而存在的  定义函数没有返回值
    mutations: {
        // changeValue (state, val) {
        //     state.value = val;
        // },
        // add (state, n) {
        //     setTimeout(function () {
        //         state.num += n;
        //     }, 1000)

        // },
        // sub (state, n) {
        //     state.num -= n;
        // }
        
        favourite_File (state) {
            console.log (state.nowTag);
            if (state.nowTag.target.innerHTML !== "喜欢的笔记") {
                state.favouriteList.push(state.listArr[state.nowIndex]);
            }
            // state.nowList = state.listArr[state.nowIndex];
        },
        remove_File (state) { 
            state.listArr.splice(state.nowIndex, 1)
        },
        changeStatus (state,e) {
            state.nowTag = e;
            // console.log (e);
            if (state.listArr != []) {
                state.nowList = state.listArr[0];
            }
            if (e.target.innerHTML == "所有的笔记") {
                state.zhuangtaixinjian = !state.zhuangtaixinjian;
                if (state.zhuangtaixinjian == true) {
                    if (state.zhuangtaifavourite == true) {
                        state.zhuangtaifavourite = !state.zhuangtaifavourite;
                    }
                } else if (state.zhuangtaixinjian == false) {
                    if (state.zhuangtaifavourite == true) {
                        state.zhuangtaifavourite = !state.zhuangtaifavourite;
                    }
                }
            } else if (e.target.innerHTML == "喜欢的笔记") {
                state.zhuangtaifavourite = !state.zhuangtaifavourite;
                if (state.zhuangtaifavourite == true) {
                    if (state.zhuangtaixinjian == true) {
                        state.zhuangtaixinjian = !state.zhuangtaixinjian;
                    }
                } else if (state.zhuangtaifavourite == false) {
                    if (state.zhuangtaixinjian == true) {
                        state.zhuangtaixinjian = !state.zhuangtaixinjian;
                    }
                }
            }
        },
        add_file: function (state) {
            let qishi = {
              title: '新笔记',
              text: '',
              isFavourite: 'false',
            };
            state.listArr.push(qishi);
            state.nowList = qishi;
        },
        set_Text(state, nowText) {
            state.nowList.text = nowText;
        },
        // changeTitle (state, valTitle) {
        //     state.title = valTitle;
        // },
        set_Title(state, nowTitle) {
            state.nowList.title = nowTitle;
        },
        set_nowNote (state, obj)
        {
          state.nowList = obj.item;
          state.nowIndex = obj.index;
        },
    
    },
    //类似于计算属性  computed  有返回值
    getters: {

        // doubleNum (state) {
        //     return state.num * 2
        // },
        // tripleNum (state, getters) {
        //     return state.num + getters.doubleNum
        // }

    },
    //为了进行异步操作而产生的
    //具体是的当前的vue实例的镜像  指向的是 vue实例
    actions: {
        // asyncAdd (store, n) {
        //     setTimeout(function () {
        //         //拿到mutations中的add方法
        //         store.commit('add', n)
        //     }, 1000)
        // }
    },
    modules: {

    }
    //     map: {
    //         namespaced: true,
    //         state: {
    //             num: 1,

    //         },
    //         getters: {
    //             doubleNum (state, getters) {

    //             }
    //         },
    //         getters,
    //         modules,
    //     },

    // }
})


